(function() {
  var ImportProjectButtonDirective, module;

  module = angular.module("taigaCommon");

  ImportProjectButtonDirective = function($rs, $confirm, $location, $navUrls) {
    var link;
    link = function($scope, $el, $attrs) {
      $el.on("click", ".import-project-button", function(event) {
        event.preventDefault();
        $el.find("input.import-file").val("");
        return $el.find("input.import-file").trigger("click");
      });
      return $el.on("change", "input.import-file", function(event) {
        var file, loader, onError, onSuccess;
        event.preventDefault();
        file = event.target.files[0];
        if (!file) {
          return;
        }
        loader = $confirm.loader("Uploading dump file");
        onSuccess = function(result) {
          var ctx, message, title;
          loader.stop();
          if (result.status === 202) {
            title = "Our Oompa Loompas are importing your project";
            message = "This process could take a few minutes <br/> We will send you an email when ready";
            return $confirm.success(title, message);
          } else {
            ctx = {
              project: result.data.slug
            };
            $location.path($navUrls.resolve("project-admin-project-profile-details", ctx));
            return $confirm.notify("success", "Your project has been imported successfuly.");
          }
        };
        onError = function(result) {
          var errorMsg, ref;
          loader.stop();
          console.log("Error", result);
          errorMsg = "Our oompa loompas have some problems importing your dump data. Please try again. ";
          if (result.status === 429) {
            errorMsg = "Sorry, our oompa loompas are very busy right now. Please try again in a few minutes. ";
          } else if ((ref = result.data) != null ? ref._error_message : void 0) {
            errorMsg = "Our oompa loompas have some problems importing your dump data: " + result.data._error_message;
          }
          return $confirm.notify("error", errorMsg);
        };
        loader.start();
        return $rs.projects["import"](file, loader.update).then(onSuccess, onError);
      });
    };
    return {
      link: link
    };
  };

  module.directive("tgImportProjectButton", ["$tgResources", "$tgConfirm", "$location", "$tgNavUrls", ImportProjectButtonDirective]);

}).call(this);
