(function() {
  var I18nDirective, I18nService, bindOnce, defaults, module, taiga,
    extend = function(child, parent) { for (var key in parent) { if (hasProp.call(parent, key)) child[key] = parent[key]; } function ctor() { this.constructor = child; } ctor.prototype = parent.prototype; child.prototype = new ctor(); child.__super__ = parent.prototype; return child; },
    hasProp = {}.hasOwnProperty;

  taiga = this.taiga;

  bindOnce = this.taiga.bindOnce;

  defaults = {
    ns: "app",
    fallbackLng: "en",
    async: false,
    lng: "en"
  };

  I18nService = (function(superClass) {
    extend(I18nService, superClass);

    function I18nService(rootscope, localesEn) {
      this.rootscope = rootscope;
      this.options = _.clone(defaults, true);
      this.options.resStore = {
        en: {
          app: localesEn
        }
      };
    }

    I18nService.prototype.setLanguage = function(language) {
      i18n.setLng(language);
      this.rootscope.currentLang = language;
      return this.rootscope.$broadcast("i18n:changeLang", language);
    };

    I18nService.prototype.initialize = function() {
      i18n.init(this.options);
      return this.rootscope.t = i18n.t;
    };

    I18nService.prototype.t = function(path, opts) {
      return i18n.t(path, opts);
    };

    return I18nService;

  })(taiga.Service);

  I18nDirective = function($rootscope, $i18n) {
    var link;
    link = function($scope, $el, $attrs) {
      var i, len, ns, options, opts, ref, results, v, values;
      values = $attrs.tr.split(",");
      options = $attrs.trOpts || '{}';
      opts = $scope.$eval(options);
      results = [];
      for (i = 0, len = values.length; i < len; i++) {
        v = values[i];
        if (v.indexOf(":") === -1) {
          results.push($el.html(_.escape($i18n.t(v, opts))));
        } else {
          ref = v.split(":"), ns = ref[0], v = ref[1];
          results.push($el.attr(ns, _.escape($i18n.t(v, opts))));
        }
      }
      return results;
    };
    return {
      link: link,
      restrict: "A",
      scope: false
    };
  };

  module = angular.module("taigaBase");

  module.service("$tgI18n", ["$rootScope", "localesEn", I18nService]);

  module.directive("tr", ["$rootScope", "$tgI18n", I18nDirective]);

}).call(this);
