(function() {
  var bindOnce, module, taiga, tgMarkitupDirective;

  taiga = this.taiga;

  bindOnce = this.taiga.bindOnce;

  module = angular.module("taigaCommon");

  tgMarkitupDirective = function($rootscope, $rs, $tr, $selectedText, $template) {
    var link, previewTemplate;
    previewTemplate = $template.get("common/wysiwyg/wysiwyg-markitup-preview.html", true);
    link = function($scope, $el, $attrs, $model) {
      var closePreviewMode, element, markdownCaretPositon, markdownSettings, markdownTitle, preview, previewDomNode, removeEmptyLine, setCaretPosition;
      element = angular.element($el);
      previewDomNode = $("<div/>", {
        "class": "preview"
      });
      closePreviewMode = function() {
        element.parents(".markdown").find(".preview").remove();
        return element.parents(".markItUp").show();
      };
      $scope.$on("markdown-editor:submit", function() {
        return closePreviewMode();
      });
      preview = function() {
        var markItUpDomNode, markdownDomNode;
        markdownDomNode = element.parents(".markdown");
        markItUpDomNode = element.parents(".markItUp");
        return $rs.mdrender.render($scope.projectId, $model.$modelValue).then(function(data) {
          var markdown;
          markdownDomNode.append(previewTemplate({
            data: data.data
          }));
          markItUpDomNode.hide();
          markdown = element.closest(".markdown");
          return markdown.on("mouseup.preview", ".preview", function(event) {
            var target;
            event.preventDefault();
            target = angular.element(event.target);
            if (!target.is('a') && $selectedText.get().length) {
              return;
            }
            markdown.off(".preview");
            return closePreviewMode();
          });
        });
      };
      markdownCaretPositon = false;
      setCaretPosition = function(elm, caretPos) {
        var range;
        if (elm.createTextRange) {
          range = elm.createTextRange();
          range.move("character", caretPos);
          return range.select();
        } else if (elm.selectionStart) {
          elm.focus();
          return elm.setSelectionRange(caretPos, caretPos);
        }
      };
      removeEmptyLine = function(textarea, line, currentCaretPosition) {
        var lines, removedLineLength;
        lines = textarea.value.split("\n");
        removedLineLength = lines[line].length;
        lines[line] = "";
        textarea.value = lines.join("\n");
        return currentCaretPosition - removedLineLength + 1;
      };
      markdownSettings = {
        nameSpace: "markdown",
        onShiftEnter: {
          keepDefault: false,
          openWith: "\n\n"
        },
        onEnter: {
          keepDefault: false,
          replaceWith: (function(_this) {
            return function(data) {
              var breakLineAtBeginning, cursorLine, emptyListItem, lastLine, lines, match, newLineContent;
              lines = data.textarea.value.split("\n");
              cursorLine = data.textarea.value.slice(0, +(data.caretPosition - 1) + 1 || 9e9).split("\n").length;
              newLineContent = data.textarea.value.slice(data.caretPosition).split("\n")[0];
              lastLine = lines[cursorLine - 1];
              match = lastLine.match(/^(\s*- ).*/);
              if (match) {
                emptyListItem = lastLine.match(/^(\s*)\-\s$/);
                if (emptyListItem) {
                  markdownCaretPositon = removeEmptyLine(data.textarea, lines.length - 1, data.caretPosition);
                } else {
                  breakLineAtBeginning = newLineContent.match(/^(\s*)\-\s/);
                  if (!breakLineAtBeginning) {
                    if (match) {
                      return "\n" + match[1];
                    }
                  }
                }
              }
              match = lastLine.match(/^(\s*\* ).*/);
              if (match) {
                emptyListItem = lastLine.match(/^(\s*\* )$/);
                if (emptyListItem) {
                  markdownCaretPositon = removeEmptyLine(data.textarea, lines.length - 1, data.caretPosition);
                } else {
                  breakLineAtBeginning = newLineContent.match(/^(\s*)\*\s/);
                  if (!breakLineAtBeginning) {
                    if (match) {
                      return "\n" + match[1];
                    }
                  }
                }
              }
              match = lastLine.match(/^(\s*)(\d+)\.\s/);
              if (match) {
                emptyListItem = lastLine.match(/^(\s*)(\d+)\.\s$/);
                if (emptyListItem) {
                  markdownCaretPositon = removeEmptyLine(data.textarea, lines.length - 1, data.caretPosition);
                } else {
                  breakLineAtBeginning = newLineContent.match(/^(\s*)(\d+)\.\s/);
                  if (!breakLineAtBeginning) {
                    return "\n" + (match[1] + (parseInt(match[2], 10) + 1)) + ". ";
                  }
                }
              }
              return "\n";
            };
          })(this),
          afterInsert: function(data) {
            var caretPosition, line, scrollRelation, totalLines;
            if (markdownCaretPositon) {
              setCaretPosition(data.textarea, markdownCaretPositon);
              caretPosition = markdownCaretPositon;
              markdownCaretPositon = false;
            } else {
              caretPosition = data.caretPosition;
            }
            totalLines = data.textarea.value.split("\n").length;
            line = data.textarea.value.slice(0, +(caretPosition - 1) + 1 || 9e9).split("\n").length;
            scrollRelation = line / totalLines;
            return $el.scrollTop((scrollRelation * $el[0].scrollHeight) - ($el.height() / 2));
          }
        },
        markupSet: [
          {
            name: $tr.t("markdown-editor.heading-1"),
            key: "1",
            placeHolder: $tr.t("markdown-editor.placeholder"),
            closeWith: function(markItUp) {
              return markdownTitle(markItUp, "=");
            }
          }, {
            name: $tr.t("markdown-editor.heading-2"),
            key: "2",
            placeHolder: $tr.t("markdown-editor.placeholder"),
            closeWith: function(markItUp) {
              return markdownTitle(markItUp, "-");
            }
          }, {
            name: $tr.t("markdown-editor.heading-3"),
            key: "3",
            openWith: "### ",
            placeHolder: $tr.t("markdown-editor.placeholder")
          }, {
            separator: "---------------"
          }, {
            name: $tr.t("markdown-editor.bold"),
            key: "B",
            openWith: "**",
            closeWith: "**"
          }, {
            name: $tr.t("markdown-editor.italic"),
            key: "I",
            openWith: "_",
            closeWith: "_"
          }, {
            name: $tr.t("markdown-editor.strike"),
            key: "S",
            openWith: "~~",
            closeWith: "~~"
          }, {
            separator: "---------------"
          }, {
            name: $tr.t("markdown-editor.bulleted-list"),
            openWith: "- "
          }, {
            name: $tr.t("markdown-editor.numeric-list"),
            openWith: function(markItUp) {
              return markItUp.line + ". ";
            }
          }, {
            separator: "---------------"
          }, {
            name: $tr.t("markdown-editor.picture"),
            key: "P",
            replaceWith: '![[![Alternative text]!]]([![Url:!:http://]!] "[![Title]!]")'
          }, {
            name: $tr.t("markdown-editor.link"),
            key: "L",
            openWith: "[",
            closeWith: ']([![Url:!:http://]!] "[![Title]!]")',
            placeHolder: $tr.t("markdown-editor.link-placeholder")
          }, {
            separator: "---------------"
          }, {
            name: $tr.t("markdown-editor.quotes"),
            openWith: "> "
          }, {
            name: $tr.t("markdown-editor.code-block"),
            openWith: "```\n",
            closeWith: "\n```"
          }, {
            separator: "---------------"
          }, {
            name: $tr.t("markdown-editor.preview"),
            call: preview,
            className: "preview-icon"
          }
        ],
        afterInsert: function(event) {
          var target;
          target = angular.element(event.textarea);
          return $model.$setViewValue(target.val());
        }
      };
      markdownTitle = function(markItUp, char) {
        var heading, i, j, n, ref;
        heading = "";
        n = $.trim(markItUp.selection || markItUp.placeHolder).length;
        for (i = j = 0, ref = n - 1; 0 <= ref ? j <= ref : j >= ref; i = 0 <= ref ? ++j : --j) {
          heading += char;
        }
        return "\n" + heading + "\n";
      };
      element.markItUp(markdownSettings);
      element.on("keypress", function(event) {
        return $scope.$apply();
      });
      return $scope.$on("$destroy", function() {
        return $el.off();
      });
    };
    return {
      link: link,
      require: "ngModel"
    };
  };

  module.directive("tgMarkitup", ["$rootScope", "$tgResources", "$tgI18n", "$selectedText", "$tgTemplate", tgMarkitupDirective]);

}).call(this);
